    // é®çä¿®æ¹æ¶ï¼blur äºä»¶ä¸­ä¼æ§è¡ä¸æ¬¡ valueChanged åï¼å°æ­¤å¼è®¾ä¸º false;
    private _valueChangeEmitted = false;
    private _mousedownEvent = null;
    private closeCalendarHandler = null;
